.footer-basic {
  padding: 20px 0;
  background-color: #83d1c4fd;
  color: #212122;
  position: relative;
  bottom: 0;
}
.footer-basic ul {
  padding: 0;
  list-style: none;
  text-align: center;
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 0;
}

.footer-basic li {
  padding: 0 10px;
}

.footer-basic ul a {
  color: inherit;
  text-decoration: none;
  opacity: 0.8;
}

.footer-basic ul a:hover {
  opacity: 1;
}

.footer-basic .social {
  text-align: center;
  padding-bottom: 25px;
}

.footer-basic .social > a {
  font-size: 24px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  border: 1px solid rgb(109, 107, 107);
  margin: 0 8px;
  color: inherit;
  opacity: 0.75;
}

.footer-basic .social > a:hover {
  opacity: 0.9;
}

.footer-basic .copyright {
  margin-top: 15px;
  text-align: center;
  font-size: 13px;
  color: rgb(83, 83, 83);
  margin-bottom: 0;
}

.list-inline-item:hover {
  cursor: pointer !important;
}
@media screen and (max-width: 480px) {
  .list-inline-item:hover {
    cursor: pointer !important;
  }
}
