@import url(https://cdn.jsdelivr.net/npm/bootstrap-icons@1.7.1/font/bootstrap-icons.css);
@import url(https://fonts.googleapis.com/css2?family=Estonia&family=Raleway:ital,wght@0,600;1,600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Maven+Pro:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.i {
  display: flex;
  height: 100vh;
}
.i-left {
  flex: 1 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.i-right {
  flex: 1 1;
  position: relative;
}

.i-left-wrapper {
  padding: 50px;
  height: 50%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.i-intro {
  font-size: 30px;
  font-weight: 300;
}
.i-name {
  font-weight: 500;
  font-size: 3.75rem;
}
.i-title {
  height: 50px;
  overflow: hidden;
}
.i-title-wrapper {
  height: 100%;
  animation: move 10s ease-in-out infinite alternate;
}
@keyframes move {
  25% {
    transform: translateY(-50px);
  }
  50% {
    transform: translateY(-100px);
  }
  75% {
    transform: translateY(-150px);
  }
}
.i-title-item {
  height: 50px;
  font-size: 30px;
  font-weight: bold;
  color: #791e94;
}
.i-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  border-radius: 10%;
}
.i-bg {
  -webkit-clip-path: polygon(100% 0%, 75% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
          clip-path: polygon(100% 0%, 75% 50%, 100% 100%, 25% 100%, 0% 50%, 25% 0%);
  background-color: #03cea4;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
}
@media screen and (max-width: 480px) {
  .i {
    flex-direction: column;
  }
  .i-left-wrapper {
    padding: 10px;
    align-items: center;
    text-align: center;
    justify-content: center;
    height: 100%;
  }
  .i-desc {
    display: none;
  }
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

body {
  background-color: whitesmoke;
  font-family: "Raleway", sans-serif;
  font-weight: 300;
}
.a {
  height: 100vh;
  display: flex;
  align-items: center;
  padding-top: 90px;
}
.a-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.a-right {
  flex: 1 1;
  max-height: 100%;
}
.a-card.bg {
  position: absolute;
  top: 50px;
  left: 50px;
  background-color: #333;
}
.a-card {
  width: 60%;
  height: 70vh;
  border-radius: 30px;
  position: relative;
  overflow: hidden;
}
.a-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 30px;
}
.a-title {
  font-weight: 500;
  font-size: 3.75rem;
}
.a-desc {
  font-weight: 400;
  text-align: justify;
  padding: 10px 10px;
  font-size: 1.25rem;
}

@media screen and (max-width: 480px) {
  .a {
    flex-direction: column;
    text-align: center;
    margin-top: 30px;
  }
  .a-left {
    width: 100%;
  }

  .a-card {
    height: 30vh;
  }
  .a-card.bg {
    display: none;
  }
  .a-title {
    font-size: 2.5rem;
  }
  .a-desc {
    font-size: 1.1rem;
  }
}

.nav-link.text-white {
  font-weight: bold;
  font-size: 1.5rem;

  font-family: "Raleway", sans-serif;
}
.text-white {
  font-size: 3.5rem;
  font-family: "Raleway", sans-serif;
}
.main {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.banner-image {
  display: none;
  background-image: url(/static/media/Pic3.63870c60.jpg);
  background-size: cover;
  background-color: rgba(16, 39, 31, 0.473);
  background-blend-mode: overlay;
}

.navbar-toggler-icon {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='30' height='30' viewBox='0 0 30 30'%3e%3cpath stroke='rgba(100, 100, 100, 0.5)' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
}
.bg-primary {
  background-color: #122932 !important;
}
.navbar-brand {
  font-family: "Maven Pro", sans-serif !important;
  font-size: 2rem !important;
}
.icon-container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px;
}
.icon-container div {
  padding: 30px;
}
.nav-link {
  cursor: pointer !important;
}
.bg-transparent {
  border: 2px solid whitesmoke;
  color: whitesmoke;
  padding: 20px;
  height: 60px;
  width: 160px;
  font-weight: 500;
  font-family: "Raleway", sans-serif;
  font-size: 30px;
  text-decoration: none;
  border-radius: 5px;
}
.bg-transparent:hover {
  color: turquoise;
  border: 2px solid turquoise;
}
.nav-item > .text-white:hover {
  color: rgba(31, 202, 202) !important;
  border-radius: 10px;
  padding: 5px;
}

.skills container {
  height: 100vh;
}
.skills {
  padding: 5rem 0 2.5rem 0;
  height: 100vh;
}
.skills > .text {
  width: 70%;
  margin: 1.5rem auto;
  text-align: center;
}
.skills .row {
  margin: 1rem 0;
}
.skills .item {
  padding: 0.5rem 0;
  margin: 0.3rem 0;
}
.skills .item-text {
  display: flex;
  justify-content: flex-start;
  font-weight: 600;
  font-size: 1.3rem;
  position: relative;
}
.skills .item-text span:last-child {
  position: absolute;
}
.skills .item-text .w-90 {
  margin-left: 85%;
}
.skills .item-text .w-75 {
  margin-left: 70%;
}
.skills .item-text .w-85 {
  margin-left: 0%;
}
.skills .item-text .w-80 {
  margin-left: 0%;
}
.skills .item-text .w-60 {
  margin-left: 0%;
}
.skills .item-text .w-68 {
  margin-left: 0%;
}
.progress {
  margin: 0.8rem 0;
  border-radius: 10px;
  height: 16px;
  width: 100%;
  background-color: #004346;
  overflow: hidden;
}
.progress-bar {
  background: #09bc8a;
  height: 100%;
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}
.progress-bar.w-90 {
  width: 90%;
}
.progress-bar.w-75 {
  width: 75%;
}
.progress-bar.w-85 {
  width: 0%;
}
.progress-bar.w-80 {
  width: 0%;
}
.progress-bar.w-60 {
  width: 0%;
}
.progress-bar.w-68 {
  width: 0%;
}
.title {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.title h2 {
  font-size: 3.75rem;
  font-family: "Raleway" sans-serif !important;
  font-weight: 500;
}
#w-85 {
  animation: python-text 2s forwards;
}
@keyframes python-text {
  100% {
    margin-left: 80%;
  }
}
#python-bar {
  animation: python-fill 2s forwards;
}

@keyframes python-fill {
  100% {
    width: 85%;
  }
}
#java-bar {
  animation: java-fill 2s forwards;
}
@keyframes java-fill {
  100% {
    width: 68%;
  }
}
#w-68 {
  animation: java-text 2s forwards;
}
@keyframes java-text {
  100% {
    margin-left: 63%;
  }
}
#html-text {
  animation: python-text 2s forwards;
}
#html-bar {
  animation: python-fill 2s forwards;
}
#w-80 {
  animation: css-text 2s forwards;
}
@keyframes css-text {
  100% {
    margin-left: 75%;
  }
}
#css-bar {
  animation: css-fill 2s forwards;
}
@keyframes css-fill {
  100% {
    width: 80%;
  }
}
#w-60 {
  animation: react-text 2s forwards;
}
@keyframes react-text {
  100% {
    margin-left: 63%;
  }
}
#react-bar {
  animation: react-fill 2s forwards;
}
@keyframes react-fill {
  100% {
    width: 68%;
  }
}
#php-text {
  animation: java-text 2s forwards;
}
#php-bar {
  animation: java-fill 2s forwards;
}
@media screen and (max-width: 480px) {
  .title h2 {
    font-size: 2.5rem;
  }
  .progress {
    height: 50%;
  }
  .skills .item-text {
    font-size: 0.89rem;
  }
  .row {
    padding: 20px;
  }

  /* .skills container{
        margin-left: 10px;
    }
     */
}

.footer-basic {
  padding: 20px 0;
  background-color: #83d1c4fd;
  color: #212122;
  position: relative;
  bottom: 0;
}
.footer-basic ul {
  padding: 0;
  list-style: none;
  text-align: center;
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 0;
}

.footer-basic li {
  padding: 0 10px;
}

.footer-basic ul a {
  color: inherit;
  text-decoration: none;
  opacity: 0.8;
}

.footer-basic ul a:hover {
  opacity: 1;
}

.footer-basic .social {
  text-align: center;
  padding-bottom: 25px;
}

.footer-basic .social > a {
  font-size: 24px;
  width: 50px;
  height: 50px;
  line-height: 50px;
  display: inline-block;
  text-align: center;
  border-radius: 50%;
  border: 1px solid rgb(109, 107, 107);
  margin: 0 8px;
  color: inherit;
  opacity: 0.75;
}

.footer-basic .social > a:hover {
  opacity: 0.9;
}

.footer-basic .copyright {
  margin-top: 15px;
  text-align: center;
  font-size: 13px;
  color: rgb(83, 83, 83);
  margin-bottom: 0;
}

.list-inline-item:hover {
  cursor: pointer !important;
}
@media screen and (max-width: 480px) {
  .list-inline-item:hover {
    cursor: pointer !important;
  }
}

.c {
  height: 100%;
  align-items: center;
  margin-top: 7rem;
  display: flex;
}
.c-bg {
  width: 30px;
  background-color: #122932;
  height: 60em;
}
.c-wrapper {
  display: flex;
  padding: 50px;
  width: 100%;
}
.c-left {
  flex: 1 1;
}
.c-right {
  flex: 1 1;
  display: flex;
  /* align-items: center;
  justify-content: flex-start; */
  /* flex-direction: column; */
  padding: 0px;
}
.c-info-item {
  display: flex;
  align-items: center;
}
.c-info-item span {
  padding: 20px;
}
.c-info-item {
  font-size: 1.3rem;
  font-weight: 500;
  font-family: Arial, "Helvetica Neue", Helvetica, sans-serif;
}
.c-title {
  font-size: 3.5rem;
  font-weight: 500;
  font-family: "Raleway";
}
.c-desc {
  font-size: 1.9rem;
}
.inputs {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
}
form {
  width: 70%;
  background-image: radial-gradient(73% 147%, #eadfdf 59%, #ece2df 100%),
    radial-gradient(
      91% 146%,
      rgba(255, 255, 255, 0.5) 47%,
      rgba(0, 0, 0, 0.5) 100%
    );
  background-blend-mode: screen;
  border-radius: 30px;
  box-shadow: rgb(131, 130, 130) 5px 5px 5px 5px;
}
form h2 {
  font-size: 3.5rem;
  font-family: "Raleway", sans-serif;
  color: rgb(13, 35, 110);
  width: 100%;
  text-align: center;
  background-color: rgb(28, 148, 142);
  border-radius: 30px 30px 0px 0px;
}
form input {
  margin: 20px;
  padding: 10px;
  width: 100%;
  border-radius: 10px;
  border: 0px;
  background-color: transparent;
  font-size: 1.2rem;
}
form span {
  margin-top: 10px;
  font-size: 1.3rem;
  color: rgb(62, 109, 14);
}
form input:focus {
  background-color: whitesmoke;
}
form textarea {
  font-size: 1.2rem;
  border: none;
  border-radius: 10px;
  background-color: transparent;
  padding: 5px;
}

form textarea:focus {
  background-color: whitesmoke;
}
form p {
  color: red;
  text-align: left;
  align-self: flex-start;
  margin-left: 5px;
}
button {
  margin-top: 20px;
}
@media screen and (max-width: 480px) {
  .c {
    padding: 0;
    height: 100%;
  }
  .c-wrapper {
    flex-direction: column;
    padding: 0px 30px;
    margin-bottom: 40px;
  }
  .c-bg {
    display: none;
  }
  .c-title {
    font-size: 30px;
  }

  .c-info-item {
    margin: 20px 0px;
    width: 100%;
  }

  .c-right {
    padding: 0px;
    margin: 10px;
  }
  .c-info-item {
    font-size: 1rem;
  }
  .c-left {
    margin: 0px;
    padding-bottom: 50px;
  }
  form {
    width: 100%;
    background-image: none;
    box-shadow: none;
    display: flex;
    justify-content: space-between;
    padding: 5px;
  }
  form h2 {
    font-family: "Raleway", sans-serif;
    font-size: 1.8rem;
    display: none;
  }
  .inputs {
    display: flex;
    justify-content: flex-start;
    width: 100%;
    padding: 0px;
    margin: 0px;
  }
  form p {
    margin: 0px;
  }

  button {
    margin-bottom: 0px;
    padding: 10px;
    font-size: 1rem;
  }
  form input {
    margin: 0px;
    margin-top: 15px;
    padding: 5px;
    font-size: 1rem;
    border-bottom: 1px solid black;
  }
  form textarea {
    font-size: 1rem;
    border-bottom: 1px solid black;
    margin-top: 20px;
    padding: 5px;
  }
}

